nav {
  height: 100%;
  z-index: 100;
  background-color: #000;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

#nav-dots {
  width: 35%;
  min-width: 100px;
  max-width: 200px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

#nav-dots ol {
  width: 100%;
  padding-top: 100%;
  position: relative;
}

#nav-dots li {
  width: 7%;
  height: 7%;
  background-color: #fff;
  border-radius: 100%;
  position: absolute;
  transform: translate(-50%, -50%);
}

#nav-dots li.focus {
  width: 10%;
  height: 10%;
}

#nav-dots li.wrong {
  background-color: #ff8c8c;
}

#nav-dots li:nth-child(n+1) {
  top: 0;
}

#nav-dots li:nth-child(n+4) {
  top: 50%;
}

#nav-dots li:nth-child(n+7) {
  top: 100%;
}

#nav-dots li:nth-child(3n+1) {
  left: 0;
}

#nav-dots li:nth-child(3n+2) {
  left: 50%;
}

#nav-dots li:nth-child(3n+3) {
  left: 100%;
}

#nav-pattern {
  width: 100%;
  height: 100%;
  z-index: 110;
  margin: 0;
  padding: 0;
  position: absolute;
}

#nav-dots img {
  width: 105%;
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
}

nav > a[href="/"] {
  width: 100%;
  height: 65px;
  position: absolute;
  top: 0;
}

@keyframes nav-shrink {
  0% {
    height: 100%;
  }

  100% {
    height: 65px;
  }
}

nav.small {
  animation-name: nav-shrink;
  animation-duration: 1.5s;
  animation-timing-function: cubic-bezier(.75, 0, .28, 1);
  animation-delay: 1.2s;
  animation-fill-mode: forwards;
}

@keyframes dot-shrink {
  0% {
    width: 35%;
    min-width: 100px;
    max-width: 200px;
  }

  100% {
    width: 40px;
    min-width: 40px;
  }
}

nav.small #nav-dots {
  animation-name: dot-shrink;
  animation-duration: 2s;
  animation-timing-function: cubic-bezier(.57, 0, .58, 1);
  animation-delay: .2s;
  animation-fill-mode: forwards;
}

@keyframes dot-disappear {
  0% {
    background-color: #fff;
  }

  100% {
    background-color: #0000;
  }
}

nav.small #nav-dots li {
  animation-name: dot-disappear;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: .2s;
  animation-fill-mode: forwards;
}

#nav-pattern {
  justify-content: center;
  align-items: center;
  display: flex;
}

#nav-pattern > img {
  width: 36.75%;
  max-width: 210px;
  min-width: 105px;
  opacity: 0;
  transition: opacity .3s;
  position: absolute;
  inset: auto;
}

#nav-pattern > .nav-link {
  margin: 10% 5% auto;
}

#nav-pattern > .nav-link > img {
  width: 50px;
  opacity: .7;
}

#nav-pattern > .nav-link:hover + img {
  opacity: 1;
}

